.phone-burger { height: 45px; z-index: 1; }

.phone-burger * { padding: 0; margin: 0; box-sizing: border-box; }

.phone-burger .wrapper { width: 320px; color: white; text-align: center; height: 40px; font-family: "Arial"; }

.phone-burger #check-menu { display: none; }

.phone-burger label { display: block; position: absolute; top: 0; left: 0; width: 100%; height: 100%; cursor: pointer; z-index: 2; line-height: 40px; }

.phone-burger .burger-line { position: absolute; top: 8px; right: 10px; width: 35px; height: 2px; background-color: #fff; transition: 0.5s all; }

.phone-burger .second, .phone-burger .third { top: 18px; }

.phone-burger .fourth { top: 28px; }

.phone-burger .main-menu { display: block; position: absolute; width: 100%; bottom: 0; left: 0; transform: translateY(100%); max-height: 0; font-size: 0; overflow: hidden; transition: 0.5s all; }

.phone-burger a { display: block; width: 100%; padding: 10px; color: white; text-decoration: none; border-top: 1px solid rgba(255, 255, 255, 0.3); background-color: #4298d5; }

.phone-burger a:hover { background-color: rgba(0, 0, 0, 0.9); }

.phone-burger #check-menu:checked ~ .main-menu { max-height: 500px; font-size: 16px; }

.phone-burger #check-menu:checked ~ .first { display: none; }

.phone-burger #check-menu:checked ~ .second { transform: rotate(45deg); }

.phone-burger #check-menu:checked ~ .third { transform: rotate(-45deg); }

.phone-burger #check-menu:checked ~ .fourth { display: none; }

